<template>
  <div class="container">
    <div class="row" style="margin: 0px;">
      
    </div>
    <div class="row">
      <h1 class="col-md-8">{{ Header }}</h1>
    </div> <br /> 
    <table v-if="token != empty" striped hover class="table" >
      <thead>
        <th>No. </th>
        <th>Nama Guru</th>
        <th>Jumlah Kehadiran</th>
      </thead>
      <tr v-for="data in guru.data.results">
        <td>{{ data.id }}</td>
        <td><router-link to='Detail?='>{{ data.display_name }}</router-link></td>
        <td>0</td>
      </tr>
    </table>

    <b-alert show variant="danger" v-else>
      Anda harus login terlebih dahulu ! <router-link to='Login'>Login Disini</router-link>.
    </b-alert>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Hello',

  data () {
    return {
      Header: 'Rekap Kehadiran Guru',
      guru: {},
      results: '',
      username: localStorage.getItem('username'),
      token: localStorage.getItem('loginToken'),
    }
  },
  mounted(){
    this.getGuru();
  },

  methods: {
    async getGuru(){
      var guru = await axios.get('http://edubox.pinisi.io/tarqi/api/list/user', {
        params: {
          model: 'user',
          token: localStorage.getItem('loginToken'),
          type: 'teachers'
        }
      });
      this.guru = guru.data
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


