<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
      <div class="d-flex flex-column flex-md-row align-items-center p-3 px-md-4 mb-3 bg-white border-bottom box-shadow">
      <h5 class="my-0 mr-md-auto font-weight-normal">
        <p class="lead pcustom" style="margin: 10px;">
          <router-link to='/' style="text-decoration: none; color: black;">
            Tar-Q
          </router-link>
        </p>
      </h5>
      <div id="container">
        <div v-if="token == empty" >
          <nav>
            <router-link to="Login"><b-button class="btn btn-outline-primary" variant="primary">Login</b-button></router-link> &nbsp;
            <router-link to="Daftar"><b-button class="btn btn-outline-success" variant="success">Daftar</b-button></router-link>
          </nav>
        </div>
        <div v-else>
          <nav class="my-2 my-md-0 mr-md-3">
            Welcome, {{ username }} | &nbsp; 
            <b-button  v-on:click="Logout" class="btn btn-outline-danger" variant="danger">Logout</b-button>
          </nav>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      token: localStorage.getItem('loginToken'),
      username: localStorage.getItem('username')
    }
  }, 

  methods: {
    Logout () {
      localStorage.removeItem("loginToken");
      localStorage.removeItem("email");
      localStorage.removeItem("role");
      localStorage.removeItem("username");

      window.location = '/';
      // Location.reload(true);
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
  color: #2c3e50;
  margin-top: 0;
  margin-bottom : 0;
}.boxes{
  padding:15px;
  width: 100%;
  margin-top: 0%;
  background-color: rgb(41, 151, 151);
  margin-bottom: 40px;
  color: black;
  padding-bottom: 30px;
}pcustom{
  color:white;
}
</style>


