<template>
  <div class="container">
    <h1>{{ detailHeader }}</h1>
    <!-- <ul v-if="lesson.data">
      <li v-for="data in lesson.data.result">
        {{ data.nama }}
      </li>
    </ul> -->

    <table class="table table-striped table-hover">
      <thead class="table-sm">
        <tr>
          <th scope="col" rowspan="2">Nama Kelas</th>
          <th scope="col" colspan="8">Pertemuan</th>
          <th rowspan="2">Jumlah</th>
        </tr>
        <tr>
          <th>1</th>
          <th>2</th>
          <th>3</th>
          <th>4</th>
          <th>5</th>
          <th>6</th>
          <th>7</th>
          <th>8</th>
        </tr>
      </thead> 
      <tr v-for="data in lesson.data.result">
        <td>{{ data.lesson_name }}</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>Hadir</td>
        <td>8</td>
      </tr>
      <tr>
        <th colspan="9">Total Jumlah</th>
        <th>----</th>
      </tr>
    </table>

    <div class="row" style="margin-bottom: 10px;">
      <b-button variant="outline-success" href="/">Kembali</b-button>
    </div>
 </div>
</template>

<script>
import axios from 'axios'
import uniq from 'lodash/uniq'
import _ from 'underscore'
export default {
  name: 'Detail',
  data () {
    return {
      detailHeader: 'Detail Rekap Kehadiran Guru',
      lesson: {}
    }
  },
  mounted(){
    this.getLesson();
  },
  methods: {
    async getLesson(){
      var lesson = await axios.get('http://edubox.pinisi.io/tarqi/api/list/lesson', {
        params: {
          model: 'absensi',
          token: localStorage.getItem('loginToken'),
          semester: '2',
          tahun_ajaran: '2018'
        }
      });
      // var groupedLesson = this.groupBy(lesson.data)
      _.groupBy(lesson.data, function(data){
        return data.lesson_name
      })
      this.lesson = lesson.data
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


