<template>
  <div class="container center">
    <!-- @submit.prevent="userLogin()" <script type="text/x-template" id="login-form"> -->

    <div class="col col-lg-4 offset-md-4 text-center">
      <form id="Login" v-on:submit.prevent="userLogin" class="form-signin" name="userLogin" method="post">

        <h2 class="h2 mb-3 font-weight-normal">{{ Header }}</h2>
        <div class="formgroup boxx" id="errorDialogue" hidden>
            <!-- <span>{{ errAlert }}</span>  -->
        </div>

        <div class="form-group">
          <label for="username" class="sr-only">username</label>
          <input type="text" class="form-control" id="username" v-model="username" placeholder="Username" required autofocus>
          <label for="password" class="sr-only">Password</label>
          <input type="password" class="form-control" v-model="password" id="password" placeholder="Password" required autofocus>
        </div>

        <input class="btn btn-primary btn-block" type="submit" value="Sign In">
        <!-- <b-button v-on:click.prevent="userLogin" variant="success">Sign in</b-button> -->
        <!-- <b-button type="button" href="/" variant="outline-primary">Back</b-button> -->
      </form>
     
    <!-- </script> -->
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import vue from 'Vue'
import qs from 'qs'
import rekapIndexVue from './rekapIndex.vue';
  
export default {
  name: 'Login',

  data() {
    return {
      showModal: false,
      Header: 'Sign In',
      username: '',
      password: '',
      errAlert: '',
    }
  },

  methods: {
  
  userLogin () {
      var loginFormData = new FormData();
      
      axios({
        method: 'post',
        url: 'http://edubox.pinisi.io/tarqi/api/post/?model=login&type=android',
        data: loginFormData,
        config: { headers: {'Content-Type': 'multipart/form-data' }}
      }) 
      
      .then( function ( response ) {

        if (response.data.error == "TRUE") {
          var errAlert = response.data.error_msg;
          alert(errAlert)
          // document.getElementById("errorDialogue").innerHTML = errAlert;
          
        } else {
          // console.log( 'loginToken : ', response )
          localStorage.setItem('loginToken', response.data.token)
          localStorage.setItem('role', response.data.role)
          localStorage.setItem('email', response.data.user.email)
          localStorage.setItem('username', response.data.user.name)

          window.location = '/';
        }
         
      }) .catch( function ( error ) {

        alert( error );

      })

      loginFormData.set( 'username', this.username);
      loginFormData.set( 'password', this.password);
      
    },

    countDownChanged (dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    }, showAlert () {
      this.dismissCountDown = this.dismissSecs
    }

  }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.boxx{
  padding: 20px;
  background-color: rgb(199, 199, 199);
  border-radius: 10px;
}
</style>


