import Vue from 'vue'
import Router from 'vue-router'
import qs from 'qs'
import axios from 'axios'
import rekapIndex from '@/components/rekapIndex'
import Detail from '@/components/Detail'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Daftar from '@/components/Register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/rekapIndex',
      name: 'rekapIndex',
      component: rekapIndex
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/Daftar',
      name: 'Daftar',
      component: Daftar
    },
  ]
})



