      <h5>Lihat Rekap Guru</h5>
    </router-link>
  </div>
  <div v-else>
    <h4 class="text-center">Anda belum Login</h4>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Home',
  data () {
    return {
      Header: 'Tar-Q Dashboard',
      token: localStorage.getItem('loginToken'),
    }
  }, 
}
</script>

