<template>
  <div class="container center">

    <div class="col col-lg-4 offset-md-4 text-center">
      <form id="Daftar" v-on:submit.prevent="userRegister" class="form-signin" name="userRegister" method="post">

        <h2 class="h2 mb-3 font-weight-normal">{{ Header }}</h2>
        <div class="formgroup boxx" id="errorDialogue" hidden>
        </div>

        <div class="form-group">
          <label for="displayName" class="sr-only">Nama Lengkap</label>
          <input type="text" class="form-control" id="displayName" v-model="displayName" placeholder="Nama Lengkap" required autofocus>
        </div>

        <div class="form-group">
          <label for="email" class="sr-only">Email</label>
          <input type="email" class="form-control" id="email" v-model="email" placeholder="Email" required autofocus>
        </div>

        <div class="form-group">
          <label for="username" class="sr-only">username</label>
          <input type="text" class="form-control" id="username" v-model="username" placeholder="Username" required autofocus>
        </div>

        <div class="form-group">          
          <label for="password" class="sr-only">Password</label>
          <input type="password" class="form-control" v-model="password" id="password" placeholder="Password" required autofocus>
        </div>

        <div class="form-group">
          <label for="password2" class="sr-only">Password</label>
          <input type="password" class="form-control" v-model="password2" id="password2" placeholder="Password Again" required autofocus>
        </div>

        <input class="btn btn-success btn-block" type="submit" value="Daftar">
      </form>
     
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import vue from 'Vue'
import qs from 'qs'
  
export default {
  name: 'Daftar',

  data() {
    return {
      Header: 'Registrasi Guru',
      email: '',
      displayName: '',
      username: '',
      password: '',
      password2: '',
      errAlert: '',
    }
  },

  methods: {
  
  userRegister () {
      var regFormData = new FormData();
      
      axios({
        method: 'post',
        url: 'http://edubox.pinisi.io/tarqi/api/post/?model=register&type=android',
        data: regFormData,
        config: { headers: {'Content-Type': 'multipart/form-data' }}
      }) 
      
      .then( function ( response ) {

        if (response.data.error == "TRUE") {
          // registrasi gagal  
          var errAlert = response.data.error_msg;
          alert(errAlert)
          
        } else {
          // registrasi berhasil  
          console.log(response)
          alert('Registrasi Berhasil')
        }
         
      }) .catch( function ( error ) {

        alert( error );

      })

      regFormData.set( 'username', this.username );
      regFormData.set( 'password', this.password );
      regFormData.set( 'display_name', this.displayName );
      regFormData.set( 'email', this.email );
      regFormData.set( 'password2', this.password2 );
      
    },
  }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.boxx{
  padding: 20px;
  background-color: rgb(199, 199, 199);
  border-radius: 10px;
}
</style>


